// axioshttps://www.npmjs.com/package/axios,axios
// paramsurlget,shinyway.com?key=params
// databody post,
import axios from 'axios'
// import store from '@/store/index'
import qs from 'qs' // node.js,,
import router from '@/router' // 404
// import { getToken } from '@/utils/auth'
import { Message, Loading } from 'element-ui'

// baseurl
// const BASE_URL = "https://c.y.qq.com"
import {
  baseUrl
} from '../../config/dev.env'

const BASE_URL = baseUrl;

// loadding
var loadingInstance

// axiso,
const $http = axios.create({
  baseURL: BASE_URL, // config/prod.envbaseurl
  timeout: 5000, // 
  headers:{
    'X-Custom-Header': 'foobar',
    "Content-Type": "application/x-www-form-urlencoded",
    "Access-Control-Allow-Orign": "*",
    "Access-Control-Allow-Methods": "GET,POST,PUT,DELETE"
  }
})
console.log(BASE_URL)
// 
$http.interceptors.request.use(
  config => {
    // if (store.getters.token) {
    //   config.headers['X-Token'] = getToken() // token--['X-Token']key 
    // }
    config.headers = {
      "Content-Type":"application/x-www-form-urlencoded",
      "Access-Control-Allow-Orign":"*",
      "Access-Control-Allow-Methods":"GET,POST,PUT,DELETE"
    }
    config.mode = "no-cors",
    // loadding
    loadingInstance = Loading.service({
      lock: true,
      // text: '...',
      // spinner: 'el-icon-loading',
      // background: 'rgba(0, 0, 0, 0.7)',
      // target: document.getElementsByClassName('app-main')[0]
    })
    // post
    if (config.method === 'post' || config.method === 'put' || config.method === 'delete') {
      config.data = qs.stringify(config.data)
    }
    return config
  }, error => {
    // 
    loadingInstance.close()
    Message.error('')
    return Promise.reject(error)
  })

// 
$http.interceptors.response.use(res => {
  // ,
  loadingInstance.close()
  //   1.
  if (res.data === '' || res.data.length === 0 || res.data === 'undefined' || res.data === undefined) {
    Message.error('data/undefined')
  }
  return res.data
}, error => {
  // ()
  // loadding
  loadingInstance.close()
  console.log(error) // error:message, config, code, request, response,

  //  1.
  if (error.code === 'ECONNABORTED' && error.message.indexOf('timeout') !== -1) {
    // Message.error('timeout/ ')
    Message.error('')
    // return $http.request(originalRequest);//
  }
  //  2.
  const errorInfo = error.response
  console.log(errorInfo)
  if (errorInfo) {
    // error =errorInfo.data//catch,Promise.reject
    if (errorInfo.status === 403) {
      router.push({
        path: '/error/403'
      })
    }
    // if (errorInfo.status === 500) {
    //   router.push({
    //     path: "/error/500"
    //   });
    // }
    // if (errorInfo.status === 502) {
    //   router.push({
    //     path: "/error/502"
    //   });
    // }
    // if (errorInfo.status === 404) {
    //   router.push({
    //     path: "/error/404"
    //   });
    // }
  }
  return Promise.reject(error) // (catch)
})
export default $http



